import uniRequest from 'uni-request';
export default {
	data() {
		return {
			username: '',
			password:''
		}
	},
	onLoad() {
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				console.log(res);
				if(res){
					uni.navigateTo({
						url: '../index/index'
					});
				}					
		    }
		});
	},
	methods: {
		login:function(){
			var username = this.username;
			var password = this.password;
			console.log(username)
			if(username == '' ){
				uni.showToast({
					title:'用户名不能为空！'
				})
			}else if(password == ''){
				uni.showToast({
					title:'密码不能为空！'
				})
			}
			if(username && password){
				
				uni.request({
					url: 'api/login',
					method: 'POST',
					data: {
						username: username,
						password: password,
						rememberMe:false
					},
					success: res => {
						var code = res.data.code;
						if(code == "0"){
							uni.setStorage({
								key: 'userInfo',
								data: res.data.data,
								success: function () {
									console.log('success');
								}
							})
							uni.navigateTo({
								url: '../index/index'
							});
						}else{
							uni.showToast({
								title: "密码错误"
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
			}
			
		}
	}
}
